.map {
    height:500px;
    background-color: white;
    padding: 1rem;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
    height: 100%;
}

.info-flag {
    width:100px;
    height:80px;
    background-size: cover;
    border-radius: 8px;
}

.info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
}

.info-confirmed,
.info-recoverd,
.info-deaths {
    font-size: 16px;
    margin-top: 5px;
}

.info-container {
    width: 150px;
}

.info-flag img {
    width:100px;
    border-radius: 5px;
}